.App {
  text-align: center;
  background-color: #f0f0f0; /* Light grey background */
  min-height: 100vh;
  padding: 20px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-logo-small {
  height: 100px; /* Smaller logo size */
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  color: white;
  padding: 0 20px;
}

.logo {
  display: flex;
  align-items: center;
}

.logo img {
  margin-right: 10px;
}

.buttons {
  display: flex;
  align-items: center;
}

.buttons button,
.buttons input {
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
}

.filter-buttons {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.filter-buttons button {
  margin: 0 5px; /* Remove equal spacing */
  padding: 15px 30px;
  font-size: 18px;
  cursor: pointer;
}

.content {
  padding: 20px;
}

.recipes-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* 4 recipes per row */
  gap: 20px;
  justify-items: center;
}

.recipe-item {
  cursor: pointer;
  text-align: center;
}

.recipe-item img {
  width: 300px; /* Increased width */
  height: 300px; /* Increased height */
  object-fit: cover;
}

.recipe-item p {
  margin-top: 10px;
  font-weight: bold;
  text-align: center;
}

/* New styles for ingredients list */
.recipe-details ul {
  /* list-style: disc inside; Use bullet points */
  padding: 0; /* Remove default padding */
}

.recipe-details ul li {
  display: list-item; /* Ensure each ingredient is displayed as a list item */
  margin-bottom: 5px; /* Add some space between ingredients */
}
